import React, { Component } from 'react';
import { Table } from 'antd';
import { setStudentReviewCertificate } from 'dump/data';
import { BtnDelete } from 'components/common/button';

const columns = [
  {
    key: 'operation',
    title: 'Thao tác',
    dataIndex: 'operation',
    render: () => <BtnDelete />,
  },
  {
    title: 'Mã sinh viên',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Giới tính',
    dataIndex: 'sex',
    key: 'sex',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'TBC chứng chỉ',
    dataIndex: 'TBCCertificate',
    key: 'TBCCC',
  },
  {
    title: 'Xếp loại',
    dataIndex: 'ratingCertificate',
    key: 'ratingCertificate',
  },
  {
    title: 'Lần xét',
    dataIndex: 'timesReview',
    key: 'timesReview',
  },
];

class TableCertificateNotChecked extends Component {
  state = {};
  render() {
    return <Table bordered dataSource={setStudentReviewCertificate} columns={columns} />;
  }
}

export default TableCertificateNotChecked;



