import React, { Component } from 'react';
import { Row, Col, Form, Radio, Select, Input, InputNumber } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { typeCertificate } from 'dump/data';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const RadioGroup = Radio.Group;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Loại chứng chỉ">
              {getFieldDecorator('typeCertificate', {})(
                <Select>
                  {typeCertificate.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Lần xét">
              <InputNumber min={1} max={10} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Điểm xét chứng chỉ">
              <Input placeholder="Nhập điểm xét" maxLength={3} />
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('conCerti', {})(
                <RadioGroup onChange={this.onChange} value={this.state.value}>
                  <Radio value={1}>Xét theo điểm từng học phần của chứng chỉ phải đạt</Radio>
                  <Radio value={2}>Xét theo điểm TBC chứng chỉ đạt</Radio>
                </RadioGroup>
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item>
              {getFieldDecorator('totalStudent', {})(<div>Tổng số sinh viên: 1</div>)}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoStudent;



